* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: rgb(32, 32, 32);
    color: rgb(255, 255, 255);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

@mixin verticalAlign {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}