header {

    padding-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
        width: 100px;
        margin: 5px;

    }

    h1 {
        letter-spacing: 2px;
        margin: 5px;
        color: salmon;

        &:hover {
            color: aqua;
        }
    }

    h2 {
        text-align: center;
        margin: 5px;
    }

    input[type="text"] {
        margin: 10px;
        padding: 15px;
        border-radius: 5px;
    }
}

.flags-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}