.card {
    background: rgb(78, 72, 72);
    border-style: solid;
    border-color: salmon;
    border-radius: 15px;
    margin: 5px;
    position: relative;
    width: 350px;
    text-align: center;

    padding: 10px;

    .genre {
        display: flex;

        flex-direction: column;
        align-items: flex-start;
    }

    &:hover {
        animation-name: bounce;
        animation-duration: 1s;
        animation-iteration-count: infinite;

        .img-container {
            opacity: 0.1;
            transition: opacity 0.2s ease-out 0.2s;

        }

        .info-container {
            opacity: 1;
            transition: opacity 0.2s ease-out 0.2s;
        }


    }

    @keyframes bounce {
        0% {
            top: 0;
        }

        50% {
            top: -10px;
        }

        100% {
            top: 0;
        }
    }

    .img-container {
        img {
            width: 300px;


        }
    }

    .info-container {
        width: 100%;
        display: flex;
        flex-direction: column;

    }

    .entete {
        @include verticalAlign;

        span {
            margin: 5px
        }
    }

}

h2 {
    white-space: pre-wrap;
    width: 100%;
}

p {
    text-align: justify;
}

.favorite {
    background: #b74d4d
}